import { FC } from 'react';
import { clsx } from 'clsx';

import { ButtonProps, ButtonVariant, ButtonScope } from './button.interface';

import { borderRadius } from './../../../tailwind/styles/border-radius';
import { focusRing } from './../../../tailwind/styles/focus';

export const Button: FC<ButtonProps> = ({
  variant = ButtonVariant.Primary,
  label,
  isLoading = false,
  children,
  disabled = false,
  ...props
}: ButtonProps) => {
  return (
    <button
      className={clsx(
        ButtonScope,
        `zep-${variant}`,
        borderRadius,
        focusRing,
        `bg-zep-${variant}-default`,
        // 'hover:bg-zep-primary-hover',
        // 'active:bg-zep-primary-active',
        // 'disabled:bg-zep-primary-disabled',
        'text-zep-primary-contrast',
        'focus-visible:ring-offset-2',
        'text-zep-base',
        'leading-zep-1.25',
        'px-zep-1',
        'py-zep-0.75',
        { 'disabled cursor-not-allowed': disabled },
      )}
      {...props}
    >
      {isLoading && (
        <i className="mr-zep-0_5" data-testid="loading-icon">
          loading icon
        </i>
      )}
      {label || children}
    </button>
  );
};
